body {
  padding: 0;

  margin: 0.2rem;
  border: 1px solid black;
  font-family: "Roboto", Helvetica, Arial, Lucida, sans-serif;

  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 1),
    rgb(186, 217, 237)
  );
  background-size: 500px;
  background-repeat: repeat;

  background-position: bottom;
  background-size: cover;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  h2 {
    text-align: center;
    margin: 5px 0;
    color: #006daa;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 6rem;

  a {
    align-self: center;
  }
  img:nth-child(2) {
    width: 50px;
    height: 50px;
  }
}
.product-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 20px;
  .paragraph {
    color: #006daa;
  }
}

/* ---------------Buchen Box--------------- */

.buchen-box {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  align-items: center;
  background-color: white;
  border-radius: 1rem;
  -webkit-box-shadow: 3px 2px 29px 7px rgba(0, 0, 0, 0.25);
  box-shadow: 3px 2px 29px 7px rgba(0, 0, 0, 0.25);
  border: 1px solid black;
  width: 40%;
  height: 25rem;
  div {
    transform: translate(0, -5px);
    color: #006daa;
    h5 {
      font-size: 14px;
    }
  }
  a {
    display: grid;
    place-content: center;
    background-color: #e88550;
    width: 12rem;
    height: 3rem;
    text-align: center;
    color: white;
    border-radius: 1.5rem;
  }
  img {
    width: 130px;
    height: 130px;
    background-color: #006daa;
    border-radius: 100px;
  }
}
.buchen-box a:hover {
  background-color: #eb9363;
}

.producte {
  position: relative;
  height: 20rem;
  width: 25rem;
  display: grid;
  align-items: center;
  justify-items: center;
  place-content: center;
  grid-template-columns: repeat(2, 2fr);
}

.producte img:first-child {
  transform: translate(25px, 0);
}
.producte img:nth-child(2) {
  transform: translate(-25px, 0);
}

.producte:nth-child(3) {
  position: absolute;

  /* left: 40px; */
  /* top: 180px; */
}
#last-img {
  transform: translate(-60px, -10px);
}
.producte img:nth-child(4) {
  position: absolute;
  right: -15px;
  bottom: 60px;
}

.sonder-button {
  display: grid;
  place-content: center;
  position: absolute;
  width: 10rem;
  height: 3rem;

  /* background: linear-gradient(to top, #eea076, #e6bca6, #f6e2d8, white);
   */
  background: linear-gradient(175deg, #ffffff 15%, #f6783a 100%);

  border-radius: 10px;
  color: black;
  text-decoration: none;
  text-align: center;
  border: 2px solid #e97b34;
  span {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .paragraph {
    font-size: 13px;
  }
  .logo-img {
    width: 100px;
    height: 40px;
  }
  .buchen-box {
    width: 20%;
    height: 20rem;
    div h5 {
      font-size: 8px;
    }
  }
}

@media (min-width: 1px) and (max-width: 460px) {
  .buchen-box {
    width: 90%;
    height: 27rem;
    div h2 {
      font-size: 20px;
    }
    div h5 {
      font-size: 12px;
    }
  }
  .buchen-box div {
    transform: translate(0, -10px);
  }
}

@media (min-width: 291px) and (max-width: 440px) {
  #last-img {
    transform: translate(-18px, -10px);
  }
  #thr-img {
    /* transform: translate(-100px, 0); */
    right: 30px;
  }
}

@media (min-width: 1px) and (max-width: 290px) {
  .producte {
    display: grid;
    place-content: center;
    grid-template-columns: repeat(1, auto);

    transform: translate(0);
    position: static;
    flex-direction: column;
    width: 20rem;
    height: 40rem;
    a {
      width: 6rem;
      font-size: 13px;
      transform: translate(0, 55px);
      span {
        font-size: 10px;
      }
    }
    #thr-img,
    #last-img {
      transform: translate(0, 70px);
      position: static;
    }
  }
}
.producte img:first-child,
.producte img:nth-child(2) {
  transform: translate(0, 0);
}
@media (min-width: 459px) and (max-width: 1200px) {
  .buchen-box {
    width: 40%;
    height: 30rem;
  }
}

@media (max-width: 347px) {
  .paragraph {
    font-size: 10px;
  }
}
